@import '../../style';

.screen {
  @include screen;
  @include fixed;
  @include center;
  @include column;
  gap: 2em;
  background-color: $primary;
}

.congratsContainer {
  @include column;

  h2 {
    font-size: 2.5em;
  }

  .accent {
    color: $accent;
  }
}

.trophyIcon {
  @include square(12em);
  position: relative;
  top: 0em;
  transition: all 0.3s ease-in-out;

  &:hover {
    top: 0.5em;
  }
}

.title {
  font-size: 8em;
}

.subtitle {
  font-size: 3.5em;
  max-width: 70vw;
  text-align: center;
  color: $light;
  margin: 0;
}

.button {
  @include app-button;
}
